.loader {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100vh;
  background-color: var(--lightgray);
  overflow: hidden;
  z-index: 99999;
}

.loader .circle {
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1);
          animation-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  background-color: var(--green);
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-box-shadow: 3px 3px 5px rgba(0,0,0,0.2);
          box-shadow: 3px 3px 5px rgba(0,0,0,0.2);
  z-index: 100;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.loader .circle.first-circle {
  -webkit-animation-name: loaderFirstCircle;
          animation-name: loaderFirstCircle;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  width: 60px;
  height: 60px;
}

.loader .circle.second-circle {
  -webkit-animation-name: loaderSecondCircle;
          animation-name: loaderSecondCircle;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  width: 0px;
  height: 0px;
}

.loader.ready .circle.first-circle,
.loader.ready .circle.second-circle  {
  -webkit-animation-name: ready;
          animation-name: ready;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}

.loader.ready {
  -webkit-animation-name: loaderHide;
          animation-name: loaderHide;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
