.form-check-label {
  display: inline-block;
  position: relative;
  padding-left: 38px;
  cursor: pointer;
  margin-bottom: 10px;
}

input[type=checkbox] {
  display: none;
}

.form-check-label::before {
  content: '';
  position: absolute;
  left: 0px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  display: block;
  width: 30px;
  height: 18px;
  border: 1px solid rgba(220, 53, 70, 0.5);
  background-color: rgba(220, 53, 70, 0.2);
  border-radius: 8px;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.form-check-label::after {
  content: '';
  position: absolute;
  left: 2px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  display: block;
  width: 14px;
  height: 14px;
  background-color: #dc3545;
  border: 1px solid #dc3545;
  border-radius: 50%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

input[type=checkbox]:checked + .form-check-label::before {
  border: 1px solid rgba(30, 126, 52, 0.5);
  background-color: rgba(30, 126, 52, 0.2);
}

input[type=checkbox]:checked + .form-check-label::after {
